export const HomeBanner = () => {
    const fields = [
        {
            html: <select>
                <option>1</option>
                <option>2</option>
                <option>3</option>
            </select>, label: 'Type'
        },
        {
            html: <select>
                <option>1</option>
                <option>2</option>
                <option>3</option>
            </select>, label: 'Location'
        },
        {
            html: <input type={"text"} name={"name_nb_"} placeholder="Here...." />,
            label: 'Search'
        },

    ]
    const [cTab, setCtab] = useState('tab-value-1');
    const tabs = [
        { label: "Buy", value: "tab-value-1" },
        { label: "Rent", value: "tab-value-2" },
        { label: "Sold", value: "tab-value-3" },

    ]
    return <div className="homeBanner">

        <section className="hb-title">
            <p>The standard Lorem Ipsum passage, used since the 1500s</p>
            <h1>Finibus Bonorum et Malorum</h1>
        </section>

        <section className="hb-tabs">
            {
                tabs.map((lb, index) => <label key={"hb label nb" + index}
                    htmlFor={"hb-tab" + index}>
                    <input type="radio" name="hb-tabs"
                        id={"hb-tab" + index} value={lb.value}
                        checked={cTab == lb.value}
                        onChange={() => setCtab(lb.value)} />
                    <span> {lb.label} </span>
                </label>)
            }
        </section>

        <section className="hb-form">
            <form>
                {
                    fields.map((field, index) => <div key={"field nb" + index}
                        className="formSegment">
                        <label>{field.label}</label>
                        <section>
                            {
                                field.html
                            }
                        </section>
                    </div>)
                }
                <div className="formOtherSearch">
                    <i className="mdi mdi-text-search"></i>
                    <p>
                        Other <br /> Search
                    </p>
                </div>
                <div className="formBtn">
                    <button><i className="mdi mdi-magnify"></i> Search</button>
                </div>
            </form>
        </section>
    </div>
}


export const HomeOurFeature = () => {
    return <div className="homeOurFeature">
        <HomeGlobalTitle />
        <section className="hof-features">
            <Slider {...card1Settings}>
                {
                    [0, 0, 0, 0, 0].map((feature, index) => <Card1 key={"feature nb" + index}
                        props={{
                            imgSrc: "visuels/homeInt" + (index + 1) + ".jpg",
                            index
                        }} />)
                }
            </Slider>
        </section>
    </div>
}

export const HomeWhyChooseUs = () => {
    const whys = [
        { icon: "mdi-emoticon-happy-outline" },
        { icon: "mdi-calculator-variant-outline" },
        { icon: "mdi-home-lightbulb-outline" },
        { icon: "mdi-map-marker-left-outline" },

    ]
    return <div className="homeWhyChooseUs">
        <HomeGlobalTitle />
        <section className="hwc-reasons">
            {
                whys.map((reason, index) => <div key={"why us reason" + index}
                    className="hwc-reason">
                    <i className={"mdi " + reason.icon}></i>
                    <b>The standard Lorem Ipsum</b>
                    <p>
                        There are many variations of passages of Lorem Ipsum available,
                        by injected humour,
                        or randomised words which don't look even slightly believable.
                    </p>
                </div>)
            }
        </section>
    </div>
}

export const HomeOurProperty = () => {
    const settings = {
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 800,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 512,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };
    const features = [
        { src: "house.jpg", name: "House", icon: "" },
        { src: "building6.jpg", name: "Appartement", icon: "" },
        { src: "office.jpg", name: "Office", icon: "" },
        { src: "room.jpg", name: "Room", icon: "" },
        { src: "villa.jpg", name: "Villa", icon: "dfd" },

    ]
    return <div className="homeOurProperty">
        <HomeGlobalTitle />
        <section className="hop-propertys">
            <Slider  {...settings}>
                {
                    features.map((property, index) => <div key={"our property nb" + index}
                        className="hop-property">
                        <SimpleImage props={{
                            src: "visuels/" + property.src,
                            alt: "user feature img nb" + index
                        }} />
                        <section>
                            <i className="mdi mdi-heart"></i>
                            <p>
                                {property.name}
                            </p>
                        </section>
                    </div>)
                }
            </Slider>
        </section>
    </div>
}


