import { Link } from "react-router-dom"

function toggleNavSubLinks(e) {
    e.currentTarget.nextElementSibling.classList.toggle('subLinkVisible')
    e.currentTarget.children[0].classList.toggle('mdi-rotate-90')
    // console.log(e.currentTarget)
}
function toggleNav() {
    if (!document.querySelector('.nav-mobileLinksVisible')) {
        document.querySelector('body').style.overflow = 'hidden'
        document.querySelector('.nav-mobileLinks').classList.add('nav-mobileLinksVisible')
    } else {
        document.querySelector('.nav-mobileLinks').classList.remove('nav-mobileLinksVisible')
    }
}
const Nav = () => {
    const menuLinks = [
        {
            link: "", label: "Accueil", subLinks: [
                { link: "", label: "Sub link 1" },
                { link: "", label: "Sub link 2" },
                { link: "", label: "Sub link 3" },
            ]
        },
        {
            link: "", label: "Listings", subLinks: [
                { link: "", label: "Sub link 1" },
                { link: "", label: "Sub link 2" },
                { link: "", label: "Sub link 3" },
            ]
        },
        {
            link: "", label: "Propertys", subLinks: [
                { link: "", label: "Sub link 1" },
                { link: "", label: "Sub link 2" },
                { link: "", label: "Sub link 3" },
            ]
        },
        {
            link: "", label: "Pages", subLinks: [
                { link: "", label: "Sub link 1" },
                { link: "", label: "Sub link 2" },
                { link: "", label: "Sub link 3" },
            ]
        },
        {
            link: "", label: "Contact",
        },
    ]
    return <nav className="nav">
        <section className="nav-logo">
            <i className="mdi mdi-map-marker-down"></i>
            <b>Logo</b>
        </section>

        <section className="nav-pcLinks">
            {
                menuLinks.map((lk, index) => lk.subLinks ?
                    <div key={"pc sub menu lin nb" + index}
                        className="pcMenuWithSubMenu">
                        <span onClick={(event) => toggleNavSubLinks(event)}>
                            {lk.label} <i className="mdi mdi-chevron-right"></i> </span>
                        {
                            <article>
                                {
                                    lk.subLinks.map((sk, indexa) => <Link to=""
                                        key={"pc sublinks nb" + indexa}>
                                        {sk.label}
                                    </Link>)
                                }
                            </article>
                        }
                    </div>
                    : <Link to="" key={"pc menu lin nb" + index}>
                        {lk.label}
                    </Link>)
            }
            <NavAuthAndContact />
        </section>
        <section className="mobileHambuger">
            <i className="mdi mdi-menu" onClick={() => toggleNav()}></i>
        </section>

        <aside className="nav-mobileLinks">
            <section className="n-ml-closer">
                <i className="mdi mdi-close" onClick={() => toggleNav()}></i>
            </section>
            <section className="n-ml-links">
                {
                    menuLinks.map((lk, index) => lk.subLinks ?
                        <div key={"mobil p link" + index}>
                            <span onClick={(event) => toggleNavSubLinks(event)}>
                                {lk.label}
                                <i className="mdi mdi-chevron-right"></i>
                            </span>
                            {
                                <article>
                                    {
                                        lk.subLinks.map((sk, indexa) => <Link to=""
                                            key={"mobil sublinks nb" + indexa}>
                                            {sk.label}
                                        </Link>)
                                    }
                                </article>
                            }
                        </div>

                        : <Link to="" key={"not sublink nb" + index}>
                            {lk.label}
                        </Link>)
                }
                <NavAuthAndContact />
            </section>
        </aside>

    </nav>
}


const NavAuthAndContact = () => {
    return <div className="navAuthAndContact">
        <a href="">
            <i className="mdi mdi-phone-outline"></i>
            <span>(+229) 1990 9999 </span>
        </a>
        <Link to="">
            Connectez vous <i className="mdi mdi-account-outline"></i>
        </Link>
        <Link to=""> <i className="mdi mdi-heart-outline"></i> Create Listing</Link>
    </div>
}
export default Nav;