import { Link } from "react-router-dom"

const Footer = () => {
    return <footer>
        <section className="footFastLinks">
            <div className="ffl-contact">
                <h4>Contactez nous</h4>
                <ul>
                    <li>
                        <i className="mdi mdi-heart"></i>
                        <p>
                            Cotonou ,Benin en allant <br />
                            vers...apres ......
                        </p>
                    </li>

                    <li>
                        <i className="mdi mdi-heart"></i>
                        <p>+229 123 456 890</p>
                    </li>
                    <li>
                        <i className="mdi mdi-heart"></i>
                        <p>support@gmail.fr</p>
                    </li>
                    <li>
                        <i className="mdi mdi-facebook"></i>
                        <i className="mdi mdi-twitter"></i>
                        <i className="mdi mdi-instagram"></i>
                        <i className="mdi mdi-linkedin"></i>
                    </li>
                </ul>
            </div>

            <div className="ffl-discover">
                <h4>Decouvrez nous</h4>
                <ul>
                    {
                        [0, 0, 0, 0].map((lk, index) => <li key={"discover link nb" + index}>
                            <Link to="" >
                                Lien
                            </Link>
                        </li>)
                    }
                </ul>
            </div>
            <div className="ffl-categories">
                <h4>Categories</h4>
                <ul>
                    {
                        [0, 0, 0, 0].map((lk, index) => <li key={"categori link nb" + index}>
                            <Link to="">
                                Lien
                            </Link>
                        </li>)
                    }
                </ul>
            </div>

            <div className="ffl-quick">
                <h4>Liens utiles</h4>
                <ul>
                    {
                        [0, 0, 0, 0].map((lk, index) => <li key={"quick link nb" + index}>
                            <Link to="" >
                                Lien
                            </Link>
                        </li>)
                    }
                </ul>
            </div>

            <div className="ffl-newsLetter">
                <h4>Souscrivez a notre newLetter</h4>
                <form>
                    <div className="formSegment">
                        <label>Ne vous inquietez pas ,nous n'envoyons pas de spam</label>
                        <section>
                            <input type={"email"} placeholder="Entrez votre mail" />
                        </section>
                    </div>
                </form>
            </div>
        </section>

        <section className="footCopyright">
            <p>
                Copyright &copy; Evans Dsv ,All right reserved
            </p>

            <p className="footLogo">
                <i className="mdi mdi-map-marker-down"></i>
                Logo
            </p>

            <div>
                <Link to="">Accueil</Link>
                <Link to="">Site map</Link>
                <Link to="">Privacy policy</Link>
                <Link to="">Cookie Policy</Link>

            </div>
        </section>
    </footer>
}

export default Footer;