import { SimpleImage } from "./Img"
import { Link } from "react-router-dom"
export const Card1 = ({ props }) => {
    const { imgSrc, index } = props
    return <div className="card1" >
        <SimpleImage props={{
            src: imgSrc,
            alt: "ima feature nb" + index
        }} />
        <article>
            <b>Title of feature</b>
            <p>Localisation,Cotonou,Paris</p>
            <div className="c1-detailWithIcon">
                <p>
                    <i className="mdi mdi-bed"></i>
                    <span>12 Title</span>
                </p>
                <p>
                    <i className="mdi mdi-scale-bathroom"></i>
                    <span>12 Title</span>
                </p>
                <p>
                    <i className="mdi mdi-garage"></i>
                    <span>12 Title</span>
                </p>
                <p>
                    <i className="mdi mdi-math-compass"></i>
                    <span>12 Title</span>
                </p>
            </div>
            <div>
                <p>
                    <span>$12,000</span>
                    <br />
                    <b>$7,500/mo</b>
                </p>

                <p className="c1-detailWithIcon2">
                    <i className="mdi mdi-move-resize-variant"></i>
                    <i className="mdi mdi-plus-circle-outline"></i>
                    <i className="mdi mdi-heart-outline"></i>
                </p>
            </div>
        </article>
        <section className="c1-absolute">
            <div className="c1-a-buttons">
                <button>featured</button>
                <button>for sale</button>

            </div>

            <div className="c1-a-img">
                <SimpleImage props={{
                    src: "profils/myUser.jpg",
                    alt: "user feature img nb" + index
                }} />
            </div>
        </section>
    </div>
}

export const Card3 = ({ props }) => {
    const { src, name, post, index } = props
    return <div className="card3">
        <SimpleImage props={{
            src: "profils/" + src,
            alt: "user com img nb"
        }} />
        <article>
            <b>{name}</b>
            <p>{post}</p>
        </article>
    </div>
}

